import React, { useState } from "react";
import Select from "./UI/Select";
import InputMask from "react-input-mask";

const CartItem = React.memo(({ item }) => {
  const [selectValue, setSelectValue] = React.useState(item.selected);
  const [countValue, setCountValue] = useState(item.count);

  React.useEffect(() => {
    item.selected = selectValue;
  }, [selectValue]); //eslint-disable-line

  return (
    <article className="cart-item">
      <p className="name">{item.name}</p>
      <InputMask
        className="count"
        type="text"
        mask="9999"
        placeholder="Кол-во"
        value={countValue}
        maskChar={null}
        onChange={(e) => {
          setCountValue(e.target.value);
          item.count = +e.target.value;
        }}
      />
      <Select value={selectValue} setValue={setSelectValue} />
      <p className="price">{item.prices[selectValue] * countValue} ₽</p>
    </article>
  );
});

export default CartItem;
