import React from "react";
import { AiOutlineCloseCircle } from "react-icons/ai";
import { IoMdDoneAll } from "react-icons/io";
import { CartContext } from "../App";

import { AiOutlineShoppingCart } from "react-icons/ai";
import InputMask from "react-input-mask";
import CartItem from "./CartItem";
import axios from "axios";

const Cart = () => {
  const { cart, setCartIsVisible } = React.useContext(CartContext);

  const [nameInput, setNameInput] = React.useState("");

  const [nameError, setNameError] = React.useState(false);
  const [phoneError, setPhoneError] = React.useState(false);

  const phoneRef = React.useRef();
  const [success, setSuccess] = React.useState(false);

  return (
    <div id="cart">
      {success ? (
        <div className="cart-container success">
          <IoMdDoneAll />
          <p>
            Заявка успешно отправлена. <br /> Мы свяжемся с вами в ближайшее время.
          </p>
        </div>
      ) : (
        <div className="cart-container">
          <AiOutlineCloseCircle className="close" onClick={() => setCartIsVisible(false)} />
          <h1>
            <AiOutlineShoppingCart />
            Корзина
          </h1>
          <div className="cart-items">
            {cart.length === 0 ? (
              <p className="empty">Корзина пуста...</p>
            ) : (
              cart.map((item, i) => <CartItem item={item} key={i} index={i} cart={cart} />)
            )}
          </div>
          {cart.length > 0 && (
            <form action="">
              <input
                type="text"
                placeholder="Ваше имя"
                value={nameInput}
                onChange={(e) => setNameInput(e.target.value)}
                style={{ border: nameError ? "1px solid #d13838" : undefined }}
                required
              />
              <InputMask
                type="text"
                mask="+7 (999) 999-99-99"
                placeholder="Ваш телефон"
                ref={phoneRef}
                style={{ border: phoneError ? "1px solid #d13838" : undefined }}
                required
              />
              <button
                className="send-btn"
                onClick={(e) => {
                  e.preventDefault();

                  if (
                    nameInput !== "" &&
                    phoneRef.current.value.replaceAll("-", "").replaceAll(" ", "").replace("_", "")
                      .length === 14
                  ) {
                    setNameError(false);
                    setPhoneError(false);
                    setSuccess(true);

                    axios.post("http://localhost:3001/api", {
                      name: nameInput,
                      phone: phoneRef.current.value,
                      items: cart.map((item) => {
                        return {
                          name: item.name,
                          count: item.count,
                          price: item.prices[item.selected] * item.count,
                          selected: item.selected,
                        };
                      }),
                    });

                    setTimeout(() => {
                      setCartIsVisible(false);
                      setSuccess(false);
                    }, 2500);
                  } else {
                    if (nameInput === "") {
                      setNameError(true);
                    }
                    if (
                      phoneRef.current.value
                        .replaceAll("-", "")
                        .replaceAll(" ", "")
                        .replace("_", "").length !== 14
                    ) {
                      setPhoneError(true);
                    }
                  }
                }}>
                Отправить заявку
              </button>
            </form>
          )}
        </div>
      )}
    </div>
  );
};

export default Cart;
