import React from "react";

import "./reset.css";
import "./scss/global.scss";
import "./scss/app.scss";

import Cart from "./components/Cart";

// import { useGLTF } from "@react-three/drei";
// import { catalogData } from "./data/items";

const Main = React.lazy(() => import("./sections/Main"));
const Second = React.lazy(() => import("./sections/Second"));
const Catalog = React.lazy(() => import("./sections/Catalog"));
const Partners = React.lazy(() => import("./sections/Partners"));
const Contacts = React.lazy(() => import("./sections/Contacts"));
const CartButton = React.lazy(() => import("./components/UI/CartButton"));

export const CartContext = React.createContext(null);

const App = () => {
  const [cart, setCart] = React.useState([]);
  const [cartIsVisible, setCartIsVisible] = React.useState(false);

  const cartValue = React.useMemo(() => {
    return { cart, setCart, cartIsVisible, setCartIsVisible };
  }, [cart, cartIsVisible]);

  React.useEffect(() => {
    document.body.style.overflow = cartIsVisible ? "hidden" : "visible";
  }, [cartIsVisible]);

  return (
    <CartContext.Provider value={{ ...cartValue }}>
      <Main />
      <Second />
      <Catalog />
      <Partners />
      <Contacts />
      <CartButton />
      {cartIsVisible && <Cart />}
    </CartContext.Provider>
  );
};

// useGLTF.preload(catalogData.map(({ model }) => `./assets/models/${model}`));

export default App;
