import React from "react";

const values = [
  { value: "0.4mm", title: "0.4 мм" },
  { value: "0.45mm", title: "0.45 мм" },
  { value: "0.5mm", title: "0.5 мм" },
  { value: "0.7mm", title: "0.7 мм" },
];

const Select = React.memo(({ value, setValue }) => {

  return (
    <select name="weight" id="" onChange={(e) => setValue(e.target.value)} value={value}>
      {values.map((item, i) => (
        <option key={i} value={item.value}>
          {item.title}
        </option>
      ))}
    </select>
  );
});

export default Select;
